import React from "react";
import Item from "./Item";

export const Todos = ({ argTodo, onDelete }) => {
  console.log(argTodo.map);
  return (
    <div className="container" style={{maxHeight:'40vh',overflowY:"scroll"}}>
      <table className="table">
        <thead>
          <tr>
            <th scope="col">#</th>
            <th scope="col">Title</th>
            <th scope="col">Description</th>
            <th scope="col">Delete</th>
          </tr>
        </thead>
        <tbody>
            {argTodo.length === 0
            ? "No Todos"
            : argTodo.map((todo, index) => {
                return <Item key={todo.sno} id={index+1} task={todo} onDelete={onDelete} />;
              })}
        </tbody>
      </table>
    </div>
  );
};

export default Todos;
