import React from 'react'

export const Footer = () => {
  return (
    <footer className='bg-dark text-light py-3' style={{position: 'absolute', width: '100%', top: 'calc(100vh - 50px)'}}>
      <p className="text-center">
        Copyright &copy; MyTodosList.com
      </p>
    </footer>
  )
}

export default Footer
