import React, { useState } from "react";

export default function AddTodo({addTodo}) {

    const [title, setTitle] = useState("")
    const [desc, setDesc] = useState("")

    const todoSubmit = (e) => {
        e.preventDefault();
        addTodo(title, desc)
        setTitle('')
        setDesc('')
    }
    return (
        <div className="ext-light py-3 container my-3">
            <h3>Add Todo</h3>
            <form onSubmit={todoSubmit}>
                <div className="input-group mb-3">
                    <span className="input-group-text" id="basic-addon1">Title</span>
                    <input type="text" value={title} onChange={e=>setTitle(e.target.value)} name="title" className="form-control" placeholder="Something here..." aria-label="Title" required/>
                </div>

                <div className="input-group my-3">
                    <span className="input-group-text">Description</span>
                    <textarea className="form-control" name='desc' value={desc} onChange={e=>setDesc(e.target.value)} aria-label="Description" required></textarea>
                </div>
                <button type="submit" className="btn btn-primary">
                    Submit
                </button>
            </form>
        </div>
    );
}
