import React from 'react'

export const Item = ({id, task, onDelete}) => {
  return (
    <tr >
      <th scope="row">{id}</th>
      <td>{task.title}</td>
      <td style={{overflow:'clip !important'}}>{task.desc}</td>
      <td><button className="btn btn-danger" onClick={() => onDelete(task)}>Finish</button></td>
    </tr>
  )
}

export default Item
