import "./App.css";
import Header from "./components/Header";
import Todos from "./components/Todos";
import Footer from "./components/Footer";
import AddTodo from "./components/AddTodo";
import About from "./components/About.js";
import React, { useState, useEffect } from "react";
import {
  BrowserRouter as Router,
  Routes as Switch,
  Route,
} from "react-router-dom";

function App() {
  let [todos, setTodos] = useState(
    localStorage.getItem("myTodoList") === null
      ? []
      : JSON.parse(localStorage.getItem("myTodoList"))
  );
  useEffect(() => {
    localStorage.setItem("myTodoList", JSON.stringify(todos));
  }, [todos]);

  const onDelete = (todo) => {
    console.log("gonna go brr...");
    setTodos(
      todos.filter((e) => {
        return e !== todo;
      })
    );
  };
  const addTodo = (title, desc) => {
    let sno = todos.length ? todos[todos.length - 1].sno + 1 : 0;
    const myTodo = { sno: sno, title: title, desc: desc };
    setTodos([...todos, myTodo]);
    console.log(myTodo);

  };
  return (
    <>
      <Router>
        <Header title="My Todo" searchable={false} />
        <Switch>
          <Route exact path="/" element={
              <>
                <AddTodo addTodo={addTodo} />
                <Todos argTodo={todos} onDelete={onDelete} />
              </>
            }></Route>
          <Route exact path="/about" element={<About />}></Route>
        </Switch>
        <Footer />
      </Router>
    </>
  );
}

export default App;
